.vertical-line-grid {
  max-width: 1270px;
  position: fixed;
  margin: 0 auto;
  border-left: 1px solid #E4E4E5;
  border-right: 1px solid #E4E4E5;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 2;
  pointer-events: none;
}
.vertical-line-grid:after, .vertical-line-grid:before, .vertical-line-grid__inner:after {
  content: "";
  width: 1px;
  height: 100%;
  background-color: #E4E4E5;
  position: absolute;
}
.vertical-line-grid:after {
  left: 25%;
}
.vertical-line-grid:before {
  left: 50%;
}
.vertical-line-grid__inner:after {
  left: 75%;
}
@media (max-width: 992px) {
  .vertical-line-grid:before {
    display: none;
  }
}

