.form-control {
  display: block;
  width: 100%;
  height: 40px;
  padding: 6px 12px 6px 20px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  color: #4f4f4f;
  border: 0;
  background-color: #fff;
  border: 1px solid #ffffff;
}

.form-control::-webkit-input-placeholder {
  /* Chrome */
  color: #4F4F4F;
}

.form-control:-ms-input-placeholder {
  /* IE 10+ */
  color: #4F4F4F;
}

.form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: #4F4F4F;
  opacity: 1;
}

.form-control:-moz-placeholder {
  /* Firefox 4 - 18 */
  color: #4F4F4F;
  opacity: 1;
}

.form-control:focus {
  border: 1px solid #333333;
  outline: none;
}

.form-field {
  border: 1px solid transparent;
}
.form-field--success {
  border: 1px solid #DDEFE6;
}
.form-field--error {
  border: 1px solid #EB5757;
  position: relative;
}
.form-field--error input[type=text],
.form-field--error input[type=password] {
  color: #EB5757;
}
.form-field--error:after {
  content: "*";
  color: #eb5757;
  position: absolute;
  top: 2px;
  bottom: 0;
  left: 11px;
}

.form-row {
  margin-bottom: 10px;
